import { Component, OnInit, ViewChild } from '@angular/core';
import { trigger, state, style, transition, animate } from '@angular/animations';
import { Subscription, timer } from 'rxjs';
import { NgForm } from '@angular/forms';
import { environment } from 'src/environments/environment';

@Component({
  selector: 'app-b2b',
  templateUrl: './b2b.component.html',
  styleUrls: ['./b2b.component.scss'],
  animations: [
    trigger('giftInfoAnimation', [
      state('void', style({
        opacity: 1,
        transform: 'translateY(10px)'
      })),
      state('*', style({
        opacity: 1,
        transform: 'translateY(0)'
      })),
      transition('void => *', [
        animate('200ms ease-in-out')
      ]),
      transition('* => void', [
        animate('0ms ease-in-out')
      ])
    ])
  ]
})
export class B2bComponent implements OnInit {

  input1Value: string = '';
  input2Value: string = '';
  
  /// otp
  showOtp: boolean = false;
  @ViewChild('ngOtpInput') ngOtpInputRef: any;
  countDown!: Subscription;
  counter: number = 60;
  tick = 1000;
  showResend: boolean = false
  otpLength: number = 6;
  isButtonDimmed: boolean = true;
  OtpCode: string = "";

  /// popupS
  showPopup: boolean = false;
  PopupIcon: string = "";
  PopupTitle: string = "";
  PopupDescription: string = "";
  PopupErrorIcon: string = "assets/images/error.png";
  PopupSuccessIcon: string = "assets/images/success.png";
  PopupConfirmIcon: string = "assets/images/confirm.png";
  PopupId: string = "";
  PopupStatusFunction?: () => void;
  PopupBtnName: string = "";
  PopupClose: boolean = false;

  constructor() { }

  ngOnInit(): void {
  }

  ///otp
  onOtpChange(code: string) {
    if (code.length < this.otpLength) {
      this.isButtonDimmed = true;
      return;
    } else {
      this.isButtonDimmed = false;
      this.OtpCode = code;
      // console.log(this.OtpCode)
    }
  }

  clearOtp() {
    this.ngOtpInputRef.setValue('');
    this.ngOtpInputRef.focusTo(this.ngOtpInputRef.getBoxId(0));
  }

  resendOTP() {
    this.showResend = false;
    this.counter = 60;
    this.clearOtp();
    this.startTimer();
    // this.GetResend();
  }

  ProceedOTP() {
    this.CloseOtp();
    this.openPopup(
      "title",
      this.PopupSuccessIcon,
      "Desc",
      "DoneID",
      this.ToDashboard, //() => {}
      "Done",
      false);
  }
  
  OpenOtp() {
    this.closePopup();
    this.startTimer();
    this.showOtp = true;
    (<HTMLElement>document.querySelector('body')).style.overflow = 'hidden';
  }

  CloseOtp() {
    this.stopTimer();
    this.counter = 60;
    this.showOtp = false;
    (<HTMLElement>document.querySelector('body')).style.overflow = 'initial';
  }


  ///timer
  stopTimer() {
    this.countDown.unsubscribe();
  }

  startTimer() {
    this.countDown = timer(0, this.tick).subscribe(() => {
      --this.counter
      if (this.counter <= 0) {
        this.stopTimer();
        this.showResend = true
      }
    })
  }

  
  onSubmit(form: NgForm) {
    if (!form.valid) {
      console.log('form invaild')
      return
    } else {
      console.log(form + (form.value['phoneNumber'] || form.value['accountNumber']));
      this.openPopup(
        "title",
        this.PopupConfirmIcon,
        "Desc",
        "DoneID",
        this.OpenOtp, //() => {}
        "Done",
        true);
    }
  }

  /////// redirect to dashboard
  ToDashboard() {
    window.location.href = this.getResultURL({ errorCode: '101', screenName: 'dashboard', isSuccessful: false, errorDescription: 'invalid' });
  }

  getResultURL(params: any) {
    const queryString = new URLSearchParams(params);
    return `${environment.resultPage}?${queryString}`;
  }


  /////// Handel status popup
  openPopup(title: any, icon: string, desc: any, id: string, callback: () => void, btnName: string, close: boolean) {
    this.PopupTitle = title;
    this.PopupIcon = icon;
    this.PopupDescription = desc;
    this.PopupId = id;
    this.showPopup = true;
    this.PopupStatusFunction = callback;
    this.PopupBtnName = btnName;
    this.PopupClose = close;
    (<HTMLElement>document.querySelector('body')).style.overflow = 'hidden';
  }

  closePopup() {
    this.showPopup = false;
    (<HTMLElement>document.querySelector('body')).style.overflow = 'initial';
  }

  PopupStatusBtnFunction() {
    if (this.PopupStatusFunction) {
      this.PopupStatusFunction();
    }
  }
}
